.departments {
  position: relative;
  flex-shrink: 0;
  width: 355px;
  padding: 25px;
  margin-right: 25px;

  &__btn {
    position: sticky;
    top: 80px;
    z-index: 10;
    display: flex;
    padding: 8px 20px;
    padding-left: 36px;
    margin: 10px;
    font-family: "Quicksand", sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 36px;
    color: $blue;
    background-color: $bgWhite;
    background-image: url("../images/filter.svg");
    background-repeat: no-repeat;
    background-position: 8px 12px;
    background-size: 24px 24px;
    border: 1px solid $blue;
    border-radius: 5px;
  }

  &__list {
    margin-left: 24px;
    overflow-y: auto;
  }

  &__wrap-list {
    position: sticky;
    top: 140px;
    overflow-y: auto;
  }

  &__category-wrap {
    position: relative;
  }

  &__sub-category {
    display: flex;
    flex-direction: column;
  }

  &__title-large {
    position: relative;
    display: inline-block;
    width: max-content;
    padding: 8px 0 8px 24px;
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    color: $black;
    letter-spacing: 0.0275em;
    cursor: pointer;
  }

  &__title-middle {
    position: relative;
    z-index: 2;
    display: inline-block;
    width: max-content;
    padding: 8px 0 8px 24px;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
    color: $black;
    cursor: pointer;
  }

  &__item-category {
    display: inline-block;
    width: max-content;
    padding: 8px 12px;
    margin-left: 24px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: $black;
    cursor: pointer;
  }

  &__title-large:hover,
  &__title-middle:hover,
  &__item-category:hover {
    color: $blue;
  }

  &__list-chevron {
    position: absolute;
    top: 8px;
    left: 0;
    z-index: 1;
    display: inline-block;
    width: 24px;
    height: 24px;
    fill: $gray;
    transition: transform 0.3s;
    transform: rotate(0);

    &_open {
      transition: transform 0.3s;
      transform: rotate(-90deg);
      transform-origin: center center;
    }

    &_fill {
      fill: $blue;
    }
  }
}

@media screen and (max-width: 1300px) {
  .departments {
    position: relative;
    top: 0;
    left: 0;
    z-index: 10;
    width: max-content;
    height: max-content;
    padding: 0;
    margin: 0;

    &__btn {
      position: absolute;
      top: 100px;
      left: calc(2vw + 16px);
      margin: 0;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0.018em;
      background-position: 8px 6px;
      transition: 0.3s;
    }

    &__btn_activ {
      position: fixed;
      top: 25px;
      left: 75px;
      z-index: 10;
      overflow: hidden;
      background-color: $bgGray;
      transition: 0.3s;
      transform: rotate(0);
    }

    &__wrap-list {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 9;
      display: block;
      height: 100%;
      padding: 80px 50px 0 50px;
      background-color: $bgGray;
      transition: transform 0.3s;
      transform: translate(-100%, 100%);
    }

    &__wrap-list_activ {
      transition: transform 0.3s;
      transform: translate(0, 0);
    }
  }
}

@media screen and (max-width: 1180px) {
  .departments {
    &__btn {
      margin-left: 0;
    }
  }
}
